import * as React from 'react'
import { QueryFilterProps } from '../filter/individual-filter'
import { makeDefaultSearchGeo } from '../filter'
import { Location, geoToFilter } from '../../location'
import AttributeDropdown from '../filter/attribute-dropdown'
import { makeSearchGeoId } from '../filter/search-geo-factory'
import { wktToGeo } from '../../location/geo-to-wkt'

const getGeojson = (filter: QueryFilterProps['filter']) => {
  if (filter.geojson) return filter.geojson

  if (filter.value) {
    return wktToGeo({
      wkt: filter.value,
      id: makeSearchGeoId(),
      buffer: filter.distance || 0,
      bufferUnit: 'meters',
    })
  }

  return makeDefaultSearchGeo()
}

const LocationFilter = (props: QueryFilterProps) => {
  const { filter } = props
  const geojson = getGeojson(filter)

  return (
    <React.Fragment>
      <AttributeDropdown {...props} />
      <Location
        value={geojson}
        onChange={value => {
          props.onChange(geoToFilter(value, filter.property))
        }}
      />
    </React.Fragment>
  )
}

export default LocationFilter
try {
    // @ts-ignore
    locationfilter.displayName = "locationfilter";
    // @ts-ignore
    locationfilter.__docgenInfo = { "description": "", "displayName": "locationfilter", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/query-filters/filter-input/location-filter.tsx#locationfilter"] = { docgenInfo: locationfilter.__docgenInfo, name: "locationfilter", path: "src/main/webapp/query-filters/filter-input/location-filter.tsx#locationfilter" };
}
