import * as React from 'react'
import {
  geoToLineProps,
  linePropsToGeo,
} from 'geospatialdraw/bin/coordinates/geometry/polygon-line'
import CoordinateList from './coordinate-list'
import Props from './geo-editor'

const Line: React.SFC<Props> = ({ value, onChange, coordinateUnit }) => {
  const { id, properties, coordinates, buffer, bufferUnit } = geoToLineProps(
    value
  )
  return (
    <CoordinateList
      coordinateList={coordinates}
      coordinateUnit={coordinateUnit}
      buffer={buffer}
      bufferUnit={bufferUnit}
      onChange={(coordinateListValue, bufferValue, bufferUnitValue) =>
        onChange(
          linePropsToGeo({
            id,
            coordinates: coordinateListValue,
            buffer: bufferValue,
            bufferUnit: bufferUnitValue,
            properties,
          })
        )
      }
    />
  )
}

export default Line
try {
    // @ts-ignore
    line.displayName = "line";
    // @ts-ignore
    line.__docgenInfo = { "description": "", "displayName": "line", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/location/line.tsx#line"] = { docgenInfo: line.__docgenInfo, name: "line", path: "src/main/webapp/location/line.tsx#line" };
}
catch (__react_docgen_typescript_loader_error) { }